import React, { useContext, useState } from 'react'
import {
  Button,
  Form,
  Input,
  TextArea,
  CascadePicker,
  Modal, Dialog,Checkbox
} from 'antd-mobile'
import { GlobalState } from '../../App'
import { updateListApi } from '../../services/api'
import { useNavigate,Link } from 'react-router-dom'

const Create: React.FC = () => {
  const { state } = useContext<any>(GlobalState)
  const [pickerVisible, setPickerVisible] = useState(false)
  const [submitLoading, setSubmitLoading] = useState(false)
  const [checked, setChecked] = useState(false)
  const navigate = useNavigate()
  const handleSubmit = (params: any) => {
    if(!checked){
      Dialog.alert({
        content: '',
        onConfirm: () => {},
      })
      return
    }
    Modal.confirm({
      content: '',
      onConfirm: () => {
        setSubmitLoading(true)
        updateListApi({
          ...params,
          city: params.city[1],
          userInfo: state.userInfo,
        }).then((res) => {
          console.log(res)
          //@ts-ignore
          if (res?.errcode) {
            Dialog.alert({
              content: '',
              onConfirm: () => {},
            })
          } else {
            Dialog.alert({
              content: '',
              onConfirm: () => {
                navigate(-1)
              },
            })
          }
        }).finally(() => {
          setSubmitLoading(false)
        })
      },
    })
  }

  console.log(state)
  return <Form
    layout="horizontal"
    footer={
      <>
        <Checkbox
          checked={checked}
          onChange={(val)=>setChecked(val)}
          style={{
            '--icon-size': '18px',
            '--font-size': '14px',
            '--gap': '6px',
            marginBottom:8
          }}
        >
          <Link to="/statement"></Link>
        </Checkbox>
        <Button block type="submit" color="primary" loading={submitLoading}>
          
        </Button>
      </>
    }
    onFinish={(values) => {
      handleSubmit(values)
    }}
  >
    <Form.Item
      name="company"
      label=""
      rules={[{ required: true, message: '' }]}
    >
      <Input placeholder=""/>
    </Form.Item>
    <Form.Item
      name="city"
      label=""
      rules={[{ required: true, message: '' }]}
      trigger="onConfirm"
      onClick={() => {
        setPickerVisible(true)
      }}
    >
      <CascadePicker
        title=""
        options={state.cityList}
        visible={pickerVisible}
        onClose={() => {
          setPickerVisible(false)
        }}
      >{value => value.length
        ? value.map((i: any) => i.label).join('/')
        : ''}</CascadePicker>
    </Form.Item>
    <Form.Item name="content" rules={[{ required: true, message: '' }]}>
      <TextArea placeholder="" maxLength={2000} rows={4}/>
    </Form.Item>
  </Form>
}

export default Create